@import "../../assets/styles/app";

.buyTokens {
  @include container;
  padding-top: 48px;
  padding-bottom: 24px;


  @include desktop {
    padding-top: 128px;
    padding-bottom: 64px;
  }

  .inner {
    @include inner;

    h2.title {

      p {
        @include Hind;
        font-size: 40px;
        line-height: 85%;
        letter-spacing: -0.02em;
        text-align: center;
        color: #fff;

        @include desktop {
          font-size: 100px;
        }

        &:nth-child(2) {
          margin-top: 8px;

          @include desktop {
            margin-top: 0;
          }
        }

        svg {
          position: relative;
          width: 48px;
          height: 48px;
          top: 12px;

          @include desktop {
            width: 64px;
            height: 64px;
            top: 4px;
          }
        }
      }
    }

    .startBtn {
      margin-top: 24px;
      width: 212px;
      height: 60px;
      margin-left: auto;
      margin-right: auto;

      @include desktop {
        margin-top: 32px;
      }
    }

    .wrapper {
      margin-top: 48px;
      display: flex;

      @include desktop {
        margin-top: 96px;
        flex-direction: column;
      }

      .progressMobile {
        @include onlyMobile();
        flex-shrink: 0;
        border-radius: 999px;
        width: 28px;
        backdrop-filter: blur(4px);
        background: rgba(255, 255, 255, 0.1);
      }

      .progressDesktop {
        @include onlyDesktop();
        position: relative;
        border-radius: 999px;
        height: 28px;
        backdrop-filter: blur(4px);
        background: rgba(255, 255, 255, 0.1);

        &::after {
          position: absolute;
          content: "";
          display: block;
          background: #FFF;
          left: 0;
          top: 0;
          height: 100%;
          width: calc(50% + 14px);
          border-radius: 999px;
        }
      }

      .items {
        margin-left: 20px;

        @include desktop {
          margin-left: 0;
          margin-top: 32px;
          display: grid;
          grid-template-columns: repeat(3, 1fr);
          grid-column-gap: 106px;
        }

        .item {
          position: relative;

          @include desktop {
            border: 1px solid rgba(255, 255, 255, 0.1);
            border-radius: 20px;
            padding: 20px;
          }

          &:not(:first-child) {
            margin-top: 32px;

            @include desktop {
              margin-top: 0;
            }
          }

          &::before {
            position: absolute;
            z-index: 2;
            content: "";
            display: block;
            width: 24px;
            height: 24px;
            border-radius: 50%;
            background: #010302;
            top: 4px;
            left: -46px;
          }

          p:nth-child(1) {
            font-weight: 500;
            font-size: 20px;
            line-height: 125%;
            color: #fff;
          }

          p:nth-child(2) {
            margin-top: 8px;
            @include Hind;
            font-size: 18px;
            line-height: 140%;
            color: rgba(255, 255, 255, 0.4);

            @include desktop {
              margin-top: 12px;
            }
          }

          &:nth-child(1) {

            &:before {
              @include desktop {
                left: 4px;
                top: -58px;
              }
            }

            &::after {
              position: absolute;
              z-index: 1;
              display: block;
              content: "";
              border-radius: 999px;
              width: 28px;
              background: #fff;
              top: 0;
              bottom: -64px;
              left: -48px;

              @include desktop {
                display: none;
              }
            }
          }

          &:nth-child(2) {

            &::before {
              @include desktop {
                left: 50%;
                transform: translateX(-50%);
                top: -58px;
              }
            }

          }

          &:nth-child(3) {
            &::before {
              background: rgba(255, 255, 255, 0.1);

              @include desktop {
                top: -58px;
                left: auto;
                right: 2px;
              }
            }
          }
        }
      }

    }

  }
}