@import "../../assets/styles/app";

.estimateYourInvestment {
  @include container;

  .inner {
    @include inner;
    padding-top: 48px;
    padding-bottom: 24px;

    @include desktop {
      padding-top: 128px;
      padding-bottom: 180px;
    }


    h2.title {
      @include Hind;
      font-size: 40px;
      line-height: 85%;
      letter-spacing: -0.02em;
      text-align: center;
      color: #fff;
      width: 232px;
      margin-left: auto;
      margin-right: auto;

      @include desktop {
        width: 672px;
        font-size: 100px;
      }
    }

    .text {
      margin-top: 32px;
      @include Hind;
      font-size: 18px;
      line-height: 140%;
      text-align: center;
      color: rgba(255, 255, 255, 0.4);

      @include desktop {
        width: 505px;
        margin-left: auto;
        margin-right: auto;
      }
    }

    .btn {
      width: 247px;
      height: 60px;
      margin: 32px auto 0;
    }

    .content {
      margin-top: 48px;


      @include desktop {
        margin-top: 96px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        border: 1px solid rgba(255, 255, 255, 0.1);
        border-radius: 20px;
        width: 722px;
        margin-left: auto;
        margin-right: auto;
      }

      .top {

        @include desktop {
          padding: 8px 8px 8px 0;
          order: 2;
        }

        .card {
          backdrop-filter: blur(7.300000190734863px);
          background: rgba(255, 255, 255, 0.1);
          border-radius: 16px;
          padding: 20px;

          .cardHeader {
            font-weight: 500;
            font-size: 24px;
            line-height: 125%;
            text-align: center;
            color: #fff;
          }

          .cardDescription {
            @include Hind;
            font-size: 16px;
            line-height: 140%;
            text-align: center;
            color: rgba(255, 255, 255, 0.4);
            margin-top: 8px;
          }

          .form {
            margin-top: 24px;

            .amount {
              margin-top: 10px;
              height: 70px;
              border: 1px solid rgba(255, 255, 255, 0.1);
              border-radius: 20px;
              padding-left: 20px;
              padding-right: 15px;
              display: flex;
              flex-direction: column;
              justify-content: center;

              p:nth-child(1) {
                font-size: 14px;
                line-height: 125%;
                color: rgba(255, 255, 255, 0.5);
              }

              p:nth-child(2) {
                margin-top: 2px;
                font-weight: 500;
                font-size: 16px;
                line-height: 125%;
                color: #fff;
              }
            }

          }

          .info {
            display: grid;
            grid-template-columns: 2fr 1.5fr 2fr;
            margin-top: 20px;

            @include desktop {
              margin-top: 24px;
            }

            & > div {

              &:not(:last-child) {
                border-right: 1px solid rgba(255, 255, 255, 0.1);
              }

              p {
                font-size: 16px;
                line-height: 125%;
                text-align: center;
                color: rgba(255, 255, 255, 0.4);

                &:nth-child(2) {
                  margin-top: 3px;
                  font-weight: 500;
                  color: #fff;
                }
              }
            }
          }

          .lineWrapper {
            position: relative;
            margin-top: 30px;

            .line {
              height: 28px;
              border-radius: 999px;
              background: rgba(255, 255, 255, 0.1);

              .lineInner {
                position: relative;
                background: #f7fa5a;
                border-radius: 999px;
                width: 33%;
                height: 100%;

                & > svg {
                  position: absolute;
                  top: 2px;
                  right: 2px;
                  width: 24px;
                  height: 24px;
                }
              }
            }

            .resetBlock {
              position: absolute;
              left: 60px;
              top: 40px;
              padding: 24px 16px;
              width: 209px;
              height: 130px;
              //backdrop-filter: blur(32px);
              //background: rgba(255, 255, 255, 0.1);

              svg {
                position: absolute;
                z-index: 1;
                top: 0;
                left: 0;
              }

              .infos {
                position: relative;
                z-index: 2;
                display: flex;
                justify-content: space-between;

                & > div {

                  p:nth-child(1) {
                    font-size: 14px;
                    line-height: 125%;
                    color: rgba(255, 255, 255, 0.5);
                  }

                  p:nth-child(2) {
                    margin-top: 2px;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 125%;
                    text-align: center;
                    color: #fff;
                  }
                }
              }

              .btn {
                position: relative;
                z-index: 2;
                margin-top: 12px;
                width: 100%;
                height: 38px;
              }
            }
          }


        }
      }

      .bottom {
        margin-top: 48px;

        @include desktop {
          margin-top: 0;
          order: 1;
          background-image: url(../../assets/png/EstimateYourInvestment/src.png);
          background-size: 432px 281px;
          background-repeat: no-repeat;
          background-position: left -50px bottom -114px;
        }

        .bottomHeader {
          font-weight: 500;
          font-size: 24px;
          line-height: 125%;
          text-align: center;
          color: #fff;

          @include desktop {
            padding: 24px 32px;
            text-align: left;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);
          }
        }

        .items {
          margin-top: 30px;
          border: 1px solid rgba(255, 255, 255, 0.1);
          border-radius: 20px;

          @include desktop {
            margin-top: 0;
            border: none;
            border-radius: unset;
          }

          .item {
            display: flex;
            padding: 20px;

            @include desktop {
              padding: 32px;
            }

            &:nth-child(1) {
              border-bottom: 1px solid rgba(255, 255, 255, 0.1);
            }

            svg {
              width: 24px;
              height: 24px;
            }

            p {
              margin-left: 9px;

              @include desktop {
                display: flex;
                flex-direction: column;
              }

              span {
                font-size: 18px;
                line-height: 125%;
                color: rgba(255, 255, 255, 0.4);

                &:nth-child(1) {
                  color: #fff;
                }
              }

            }
          }
        }

      }

    }

  }
}