@import "../../assets/styles/app";

.yourPresaleDashboard {
  @include container;
  padding-top: 48px;
  padding-bottom: 24px;

  @include desktop {
    padding-top: 128px;
    padding-bottom: 64px;
  }

  .inner {
    @include inner;

    h2.title {

      p {
        @include Hind;
        font-size: 40px;
        line-height: 85%;
        letter-spacing: -0.02em;
        text-align: center;
        color: #fff;

        @include desktop {
          font-size: 100px;
        }

        &:nth-child(2) {
          margin-top: 8px;

          @include desktop {
            margin-top: 10px;
          }
        }

        svg {
          position: relative;
          width: 30px;
          height: 30px;
          top: 4px;

          @include desktop {
            width: 50px;
            height: 50px;
            top: 0px;
          }
        }
      }
    }

    .description {
      margin-top: 32px;
      @include Hind;
      font-size: 18px;
      line-height: 140%;
      text-align: center;
      color: rgba(255, 255, 255, 0.4);

      @include desktop {
        width: 596px;
        margin-left: auto;
        margin-right: auto;
      }
    }

    .btn {
      width: 288px;
      height: 60px;
      margin-top: 32px;
      margin-left: auto;
      margin-right: auto;
    }

    .content {
      margin-top: 48px;

      @include desktop {
        margin-top: 96px;
        display: flex;
        flex-direction: row-reverse;
        border: 1px solid rgba(255, 255, 255, 0.1);
        border-radius: 20px;
        width: 836px;
        margin-left: auto;
        margin-right: auto;
      }

      .top {

        @include onlyDesktop();

        @include desktop {
          padding: 8px 8px 8px 0;
        }

        .card {
          height: 100%;
          position: relative;
          overflow: hidden;
          backdrop-filter: blur(32px);
          background: rgba(255, 255, 255, 0.05);
          border-radius: 20px;

          @include desktop {
            flex-shrink: 0;
            width: 414px;
          }

         img {
           display: block;
           position: absolute;
           width: 542px;
           height: 448px;
           top: 0;
           left: -60px;
         }

        }
      }

      .bottom {
        margin-top: 64px;

        @include desktop {
          margin-top: 0;
          flex-grow: 1;
        }

        .bottomTitle {
          font-weight: 500;
          font-size: 24px;
          line-height: 125%;
          text-align: center;
          color: #fff;

          @include desktop {
            padding-top: 24px;
            padding-bottom: 24px;
            padding-left: 32px;
            text-align: left;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);
          }
        }

        .bottomItems {
          border: 1px solid rgba(255, 255, 255, 0.1);
          border-radius: 20px;
          background: #010302;
          margin-top: 32px;

          @include desktop {
            border: none;
            margin-top: 0;
          }

          .bottomItem {
            padding: 20px;
            display: flex;

            @include desktop {
              padding: 32px;
            }

            &:not(:last-child) {
              border-bottom: 1px solid rgba(255, 255, 255, 0.1);
            }

            p {
              span {
                margin-left: 9px;
                font-size: 18px;
                line-height: 125%;
                color: #fff;

                &:nth-child(2) {
                  color: rgba(255, 255, 255, 0.4);
                }
              }

              @include desktop {
                display: flex;
                flex-direction: column;
              }



            }

          }
        }

      }


    }

  }


}