@import "../../assets/styles/app";

.weFund {
  @include container;
  position: relative;

  .back {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
  }

  .inner {
    @include inner;
    position: relative;
    z-index: 2;
    padding-top: 128px;

    @include desktop {
      padding-top: 136px;
    }

    .label {
      padding: 8px 24px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 999px;

      @include desktop {
        margin-left: auto;
        margin-right: auto;
        width: 370px;
      }

      p {
        font-weight: 500;
        font-size: 16px;
        line-height: 125%;
        color: #fff;
        text-align: center;

        span {
          color: rgba(255, 255, 255, 0.5);
        }
      }

    }

    .title {
      margin-top: 24px;

      @include desktop {
        margin-top: 16px;
      }

      p {
        @include Hind;
        font-size: 52px;
        line-height: 85%;
        letter-spacing: -0.02em;
        text-align: center;
        color: #fff;

        @include desktop {
          font-size: 120px;
        }

        &:nth-child(2) {
          margin-top: 6px;

          @include desktop {
            margin-top: 0;
          }

          svg {
            position: relative;
            width: 48px;
            height: 48px;
            top: 12px;

            @include desktop {
              width: 65px;
              height: 65px;
              top: 0;
            }
          }

        }
      }
    }

    .description {
      margin-top: 32px;

      @include desktop {
        width: 596px;
        margin-left: auto;
        margin-right: auto;
      }

      p {
        font-size: 18px;
        line-height: 140%;
        text-align: center;
        color: rgba(255, 255, 255, 0.4);

        span {
          color: rgba(255, 255, 255, 0.75);
        }
      }
    }

    .line {
      position: relative;
      margin-top: 32px;
      height: 7px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 999px;

      @include desktop {
        width: 597px;
        margin-left: auto;
        margin-right: auto;
      }

      .lineInner {
        position: relative;
        height: 100%;
        width: 75%;
        background: linear-gradient(270deg, #dcff06 0%, rgba(226, 253, 38, 0) 100%);
        border-radius: 0 999px 999px 0;

        &::after {
          position: absolute;
          right: 0;
          top: 0;
          height: 100%;
          width: 7px;
          content: "";
          display: block;
          border-radius: 50%;
          filter: blur(9.399999618530273px);
          background: #faec5a;
        }
      }
    }

    .lineDescription {
      margin-top: 12px;
      display: flex;
      justify-content: space-between;

      @include desktop {
        width: 597px;
        margin-left: auto;
        margin-right: auto;
      }

      p {
        font-weight: 500;
        font-size: 16px;
        line-height: 125%;
        color: #fff;
      }
    }

    .getStartedNowBtn {
      margin-top: 32px;
      margin-left: auto;
      margin-right: auto;
      width: 225px;

      @include desktop {
        height: 60px;
      }
    }

  }

  .videoWrapper {
    position: relative;
    z-index: 3;
    margin-top: 50px;

    @include desktop {
      margin-top: 64px;
    }

    .video {
      position: relative;
      width: 100%;
      height: auto;
    }

    .cards {
      @include onlyDesktop();

      .card {
        position: absolute;
        border-radius: 20px;
        backdrop-filter: blur(24px);
        background: linear-gradient(180deg, rgba(255, 255, 255, 0.1) 0%, rgba(0, 0, 0, 0.1) 100%);
        padding: 16px;
        display: flex;
        align-items: center;

        img {
          display: block;
          width: 48px;
          height: 48px;
        }

        & > div {
          margin-left: 12px;
          display: flex;
          flex-direction: column;
          justify-content: center;

          p:nth-child(1) {
            font-weight: 600;
            font-size: 18px;
            line-height: 125%;
            color: #fff;
          }

          p:nth-child(2) {
            font-size: 16px;
            line-height: 125%;
            color: rgba(255, 255, 255, 0.5);

            span {
              font-weight: 500;
              color: #46bb17;
            }
          }
        }

        &:nth-child(1) {
          left: 200px;
          bottom: 304px;
        }

        &:nth-child(2) {
          left: 725px;
          bottom: 246px;
        }

        &:nth-child(3) {
          right: 260px;
          bottom: 390px;
        }
      }
    }

  }




  .imgMobile {
    @include onlyMobile;
    position: relative;
    z-index: 3;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    width: 320px - 24px - 24px;
  }

  .imgDesktop {
    @include onlyDesktop();
    position: relative;
    z-index: 3;
    margin-top: 64px;
    width: 1392px;
    height: 1060px;
    margin-left: auto;
    margin-right: auto;
  }

}