@import "../../assets/styles/app";

.topContributors {
  @include container;
  padding-top: 48px;
  padding-bottom: 24px;

  @include desktop {
    padding-top: 128px;
    padding-bottom: 64px;
  }

  .inner {
    @include inner;

    h2.title {
      @include Hind;
      font-size: 40px;
      line-height: 85%;
      letter-spacing: -0.02em;
      text-align: center;
      color: #fff;
      width: 280px;
      margin-left: auto;
      margin-right: auto;

      @include desktop {
        width: 690px;
        font-size: 100px;
      }
    }

    .text {
      margin-top: 32px;
      @include Hind;
      font-size: 18px;
      line-height: 140%;
      text-align: center;
      color: rgba(255, 255, 255, 0.4);

      @include desktop {
        width: 596px;
        margin-left: auto;
        margin-right: auto;
      }
    }

    .btn {
      width: 247px;
      height: 60px;
      margin: 32px auto 0;
    }

    .content {
      margin-top: 48px;

      @include desktop {
        margin-top: 96px;
        display: flex;
        flex-direction: row-reverse;
        border: 1px solid rgba(255, 255, 255, 0.1);
        border-radius: 20px;
        width: 972px;
        margin-left: auto;
        margin-right: auto;
      }

      .top {

        @include desktop {
          padding: 8px 8px 8px 0;
        }

        .card {
          backdrop-filter: blur(32px);
          //background-image: url(../../assets/png/TopContributors/background.png);
          //background-size: contain;
          //background-size: 150% auto;
          //background-position: center 50%;
          background: rgba(255, 255, 255, 0.1);
          border-radius: 20px;

          @include desktop {
            flex-shrink: 0;
            width: 504px;
          }

          .cardHeader {
            padding-top: 20px;
            display: grid;
            grid-template-columns: 0.5fr repeat(3, 1fr);

            p {
              font-size: 12px;
              line-height: 125%;
              text-align: center;
              color: rgba(255, 255, 255, 0.45);
            }
          }

          .cardBody {
            .row {
              padding: 18px 0;
              display: grid;
              grid-template-columns: 0.5fr repeat(3, 1fr);

              &:not(:last-child) {
                border-bottom: 1px solid rgba(255, 255, 255, 0.1);
              }

              p, p span {
                font-size: 12px;
                line-height: 125%;
                text-align: center;
                color: #fff;
              }

              &:nth-child(1), &:nth-child(2), &:nth-child(3) {
                p:nth-child(1) {
                  position: relative;
                  z-index: 2;
                  font-weight: 500;
                  color: #030504;

                  &::before {
                    content: "";
                    position: absolute;
                    z-index: 1;
                    display: block;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    width: 20px;
                    height: 20px;
                    background: #FFF;
                    border-radius: 50%;
                  }

                  span {
                    position: relative;
                    z-index: 2;
                  }
                }
              }

              &:nth-child(1), &:nth-child(2), &:nth-child(3) {
                p:nth-child(1) {
                  span {
                    font-weight: 500;
                    color: #030504;
                  }

                }
              }

              &:nth-child(1) {
                p:nth-child(1) {
                  &::before {
                    background: #f7fa5a;
                  }
                }
              }

              &:nth-child(2) {
                p:nth-child(1) {
                  &::before {
                    background: #fa5a6d;
                  }
                }
              }

              &:nth-child(3) {
                p:nth-child(1) {
                  &::before {
                    background: #56ffe1;
                  }
                }
              }
            }
          }

        }
      }

      .bottom {
        margin-top: 64px;

        @include desktop {
          margin-top: 0;
          flex-grow: 1;
        }

        .bottomTitle {
          font-weight: 500;
          font-size: 24px;
          line-height: 125%;
          text-align: center;
          color: #fff;

          @include desktop {
            padding-top: 24px;
            padding-bottom: 24px;
            padding-left: 32px;
            text-align: left;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);
          }
        }

        .bottomItems {
          border: 1px solid rgba(255, 255, 255, 0.1);
          border-radius: 20px;
          background: #010302;
          margin-top: 32px;

          @include desktop {
            border: none;
            margin-top: 0;
          }

          .bottomItem {
            padding: 20px;
            display: flex;

            @include desktop {
              padding: 32px;
            }

            &:not(:last-child) {
              border-bottom: 1px solid rgba(255, 255, 255, 0.1);
            }

            p {
              span {
                margin-left: 9px;
                font-size: 18px;
                line-height: 125%;
                color: #fff;

                &:nth-child(2) {
                  color: rgba(255, 255, 255, 0.4);
                }
              }

              @include desktop {
                display: flex;
                flex-direction: column;
              }



            }

          }
        }

      }


    }

  }
}