@import "../../assets/styles/app";

.wrapper {
  position: relative;

  .fairDistribution {
    @include container;
    position: relative;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    //background: grey;

    .tips {

      .tipWrapper {
        position: absolute;
        left: 50%;

        &:nth-child(1) {
          top: 40px;
          transform: translateX(-50%) rotate(-4deg);

          @include desktop {
            left: 25%;
            top: calc(0.5 * (0.5 * (100vh - 386px) - 40px));
          }

        }

        &:nth-child(2) {
          top: 40px + 40px + 30px;
          transform: translateX(-50%) rotate(4deg);

          @include desktop {
            left: 50%;
            top: calc(0.5 * (0.5 * (100vh - 386px) - 40px));
          }

        }

        &:nth-child(3) {
          top: 40px + 40px + 30px + 40px + 30px;
          transform: translateX(-50%) rotate(-4deg);

          @include desktop {
            left: 75%;
            top: calc(0.5 * (0.5 * (100vh - 386px) - 40px));
          }

        }

        @include desktop {
          top: calc(0.5 * (0.5 * (100vh - 386px) - 40px));
          //transform: translateX(-50%);
        }

        .tip {
          position: relative;
          border: 1px solid rgba(255, 255, 255, 0.1);
          border-radius: 999px;
          padding: 8px 16px;
          display: flex;
          align-items: center;

          svg {
            width: 20px;
            height: 20px;
          }

          span {
            @include Hind;
            font-size: 18px;
            line-height: 140%;
            text-align: center;
            color: rgba(255, 255, 255, 0.4);
            margin-left: 10px;
          }
        }
      }


    }


    .inner {
      @include inner;

      h2.title {

        p {
          @include Hind;
          font-size: 40px;
          line-height: 85%;
          letter-spacing: -0.02em;
          text-align: center;
          color: #fff;

          @include desktop {
            font-size: 100px;
          }

          &:nth-child(2) {
            margin-top: 8px;

            @include desktop {
              margin-top: 0;
            }
          }

          svg {
            position: relative;
            width: 48px;
            height: 48px;
            top: 12px;

            @include desktop {
              width: 64px;
              height: 64px;
              top: 4px;
            }
          }
        }
      }

      .description {
        margin-top: 24px;
        font-size: 18px;
        line-height: 140%;
        text-align: center;
        color: rgba(255, 255, 255, 0.4);

        @include desktop {
          margin-top: 32px;
          width: 596px;
          margin-left: auto;
          margin-right: auto;
        }
      }

      .btn {
        width: 142px;
        height: 60px;
        margin-top: 32px;
        margin-left: auto;
        margin-right: auto;
      }

    }
  }
}




