@import "../../assets/styles/app";

.getStartedWithMetaMask {
  @include container;
  padding-top: 48px;
  padding-bottom: 24px;

  @include desktop {
    padding-top: 128px;
    padding-bottom: 64px;
  }

  .inner {
    @include inner;

    h2.title {

      p {
        @include Hind;
        font-size: 40px;
        line-height: 85%;
        letter-spacing: -0.02em;
        text-align: center;
        color: #fff;

        @include desktop {
          font-size: 100px;
        }

        &:nth-child(2) {
          margin-top: 8px;

          @include desktop {
            margin-top: 0;
          }
        }

        svg {
          position: relative;
          width: 45px;
          height: 48px;
          top: 12px;

          @include desktop {
            width: 58px;
            height: 63px;
            top: 4px;
          }
        }
      }
    }

    .description {
      margin-top: 32px;
      @include Hind;
      font-size: 18px;
      line-height: 140%;
      text-align: center;
      color: rgba(255, 255, 255, 0.4);

      @include desktop {
        width: 596px;
        margin-left: auto;
        margin-right: auto;
      }
    }

    .setUpBtn {
      width: 247px;
      height: 60px;
      margin-top: 32px;
      margin-left: auto;
      margin-right: auto;
    }

    .cards {
      margin-top: 32px;

      @include desktop {
        margin-top: 96px;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: 22px;
        width: 980px;
        margin-left: auto;
        margin-right: auto;
      }

      .card {
        border: 1px solid rgba(255, 255, 255, 0.1);
        border-radius: 20px;
        padding: 20px;

        &:not(:first-child) {
          margin-top: 20px;

          @include desktop {
            margin-top: 0;
          }
        }

        .cardHeader {
          display: flex;
          align-items: center;
          justify-content: space-between;

          p:nth-child(1) {
            font-weight: 500;
            font-size: 20px;
            line-height: 125%;
            color: #fff;
          }

          p:nth-child(2) {
            font-size: 18px;
            line-height: 125%;
            text-align: right;
            color: rgba(255, 255, 255, 0.4);
          }
        }

        video {
          display: block;
          margin-top: 16px;
          height: 200px;
          width: auto;
          margin-left: auto;
          margin-right: auto;
        }

        img {
          margin-top: 16px;
          display: block;
          width: 272px;
          height: 248px;
          margin-left: auto;
          margin-right: auto;
        }

        & > p {
          margin-top: 16px;
          font-size: 18px;
          line-height: 140%;
          color: rgba(255, 255, 255, 0.4);
        }

      }
    }




  }

}