@import "../../assets/styles/app";

.earnByBringing {
  @include container;
  padding-top: 48px;
  padding-bottom: 24px;
  overflow: hidden;

  @include desktop {
    padding-top: 128px;
    padding-bottom: 24px;
  }


  .inner {
    @include inner;

    h2.title {

      p {
        @include Hind;
        font-size: 40px;
        line-height: 85%;
        letter-spacing: -0.02em;
        text-align: center;
        color: #fff;

        @include desktop {
          font-size: 100px;
        }

        &:nth-child(2) {
          margin-top: 8px;

          @include desktop {
            margin-top: 10px;
          }
        }

        svg {
          position: relative;
          width: 48px;
          height: 48px;
          top: 12px;

          @include desktop {
            width: 64px;
            height: 64px;
            top: 4px;
          }
        }
      }
    }

    .description {
      margin-top: 32px;
      @include Hind;
      font-size: 18px;
      line-height: 140%;
      text-align: center;
      color: rgba(255, 255, 255, 0.4);

      @include desktop {
        width: 596px;
        margin-left: auto;
        margin-right: auto;
      }
    }

    .btn {
      width: 247px;
      height: 60px;
      margin-top: 32px;
      margin-left: auto;
      margin-right: auto;
    }

    .cards {
      position: relative;
      margin-top: 24px;

      @include desktop {
        margin-top: 96px;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-column-gap: 21px;
      }

      .card {
        position: relative;
        padding: 24px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);

        &:nth-child(1) {
          padding-bottom: 138px;

          @include desktop {
            padding-bottom: 20px;
          }
        }

        @include desktop {
          padding: 20px;
          border-bottom: none;
          border-right: 1px solid rgba(255, 255, 255, 0.1);
          display: block;
        }

        p:nth-child(1) {
          font-weight: 500;
          font-size: 16px;
          line-height: 125%;
          text-transform: uppercase;
          color: #fff;

          @include desktop {
            font-size: 20px;
          }

          span {
            color: #f7fa5a;
          }
        }

        p:nth-child(2) {
          font-size: 18px;
          line-height: 125%;
          text-transform: lowercase;
          color: rgba(255, 255, 255, 0.2);

          @include desktop {
            margin-top: 175px;
          }

          span {
            text-transform: uppercase;
            color: rgba(255, 255, 255, 0.4);
          }
        }

        img {
          @include onlyMobile();
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          height: 100px;
          width: auto;
          bottom: 20px;
        }

      }

      .waveWrapper {
        @include onlyDesktop();
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 65px;
        width: 1297px;
        height: 131px;

        .left {
          position: absolute;
          z-index: 2;
          top: 0;
          left: 0;
          width: 0%;
          height: 100%;
          overflow-x: hidden;

          svg {
            position: absolute;
            top: 0;
            left: 0;
          }
        }

        .center {
          position: absolute;
          z-index: 1;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }

        .right {
          position: absolute;
          z-index: 2;
          top: 0;
          right: 0;
          width: 100%;
          height: 100%;
          overflow-x: hidden;

          svg {
            position: absolute;
            top: 0;
            right: 0;
          }
        }
      }

      img {
        @include onlyDesktop();
        position: absolute;
      }
    }
  }
}